.aboutpage {
  width: 100%;
  /* padding: 15px 10px; */
  box-sizing: border-box;
}

.ab-top {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 40px;
}
.ab-top > .content {
  width: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.content > .tp {
  font-family: "Lora";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 35px;
  text-align: center;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  color: #5c5470;
  width: 80%;
}
.content > .btm {
  font-family: "Montserrat";
  font-style: normal;
  /* font-weight: 400; */
  font-size: 16px;
  line-height: 26px;
  /* or 167% */

  letter-spacing: 0.7px;
  text-transform: uppercase;

  /* color: #333333; */
  text-align: left;
}

.memobtn {
  height: 45px;
  width: 240px;
  background: #5c5470;
  border-radius: 25px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  /* or 125% */

  text-align: center;
  letter-spacing: 0.4px;
  text-transform: capitalize;

  color: #ffffff;
  cursor: pointer;
  padding: 5px;
  margin-top: 20px;
}

.ab-bottom {
  /* background: #5c5470; */
  padding: 50px 50px;
  box-sizing: border-box;
  margin-top: 30px;

  background-color: #5c5470;
  /* transform: matrix(-1, 0, 0, 1, 0, 0); */
}
.triagnle {
  height: 100px;
  background-color: #5c5470;
  clip-path: polygon(0 100%, 5% 0, 100% 100%);
  /* margin-top: 15px; */
  margin-bottom: -31px;
  overflow: hidden;
}

.ab-bottom > h3 {
  font-family: "Lora";
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 46px;
  /* identical to box height, or 131% */

  text-align: center;
  letter-spacing: 0.16px;
  text-transform: uppercase;

  color: #ffffff;
}
.ab-bottom > p {
  text-align: left;
  color: #ffffff;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  /* or 144% */

  letter-spacing: 0.16px;

  color: #ffffff;
}
