.share-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
    color: rgb(0, 0, 0);
    text-align: center;
    height: 388px;
  }
  
  
  h1 {
    font-size: 36px;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 18px;
    margin-bottom: 20px;
  }
  
  .share-mBtn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px;
    gap: 10px;
    
    width: 157px;
    height: 46px;
    
    background: #5C5470;
    border-radius: 25px;
    border: none;
    cursor: pointer;
    color: white;
    flex: none;
    order: 2;
    flex-grow: 0;
    
    
  }
  
 

  .share-main-text{
    width: 80%;
    font-family: 'Lora';
    font-style: normal;
    font-weight: 500;
    font-size: 35px;
    line-height: 61px;
    /* identical to box height, or 174% */
    
    text-align: center;
    letter-spacing: 0.2px;
    text-transform: uppercase;
    
    color: #5C5470;
  }
